@font-face {
  font-family: 'Montserrat-Extrabold';
  src: local('Montserrat-Extrabold'),
  url('../src/assets/font/Montserrat-ExtraBold.otf')
}

@font-face {
  font-family: 'NoeDisplay-Bold';
  src: local('NoeDisplay-Bold'),
  url('../src/assets/font/NoeDisplay-Bold.ttf')
}

@font-face {
  font-family: 'Tuesday Night';
  src: local('Tuesday Night')
  url('')
       url('/src/assets/font/TuesdayNight-Regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('/src/assets/font/TuesdayNight-Regular.woff') format('woff'), /* Pretty Modern Browsers */
       url('/src/assets/font/TuesdayNight-Regular.ttf')  format('truetype'), /* Safari, Android, iOS */
       url('/src/assets/font/TuesdayNight-Regular.svg') format('svg'); /* Legacy iOS */
}

.NavText {
  font-family: 'Montserrat-Extrabold';
  float: right;
}

.App {
  margin-top: 5vw;
  text-align: center;
  font-family: 'NoeDisplay-Bold';
  font-size: 5vw;
  display: inline;
}

.TeusdayHeader {
  text-align: center;
  font-family: 'Tuesday Night';
  font-size: 15vw;
  background: linear-gradient(90deg, rgba(235,51,73,1) 0%, rgba(229,37,56,1) 58%, rgba(244,128,67,1) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  padding-top: 7vw;
}

.App-logo {
  height: 70vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 5s linear;
  }
}

.App-header {
  background-color: #FFFFFF;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
